.footer {
  color: $gray-light;
  &__inner {
    @include flex(space-between, center);
    padding-top: 25px;
    padding-bottom: 25px; }
  &__left {
    @include flex(flex-start, center); }
  &__right {
    text-align: right; }
  &__copy {
    text-transform: uppercase;
    margin-right: 15px; } }
